<template>
  <div>
    <el-dialog width='30%' title="修改密码" :visible.sync="oldPasswordDialog">
    <el-row>
      <el-col :span='22'>
        <el-form>
    <el-form-item label="原始密码" label-width="100px">
      <el-input v-model="oldPassword" placeholder="请输入原密码"/>
    </el-form-item>
  </el-form>
      </el-col>
    </el-row>
  <div slot="footer" class="dialog-footer">
    <el-button type="primary" @click='clickInputOldPassword'>下一步</el-button>
  </div>
</el-dialog>
    <el-dialog width='30%' title="修改密码" :visible.sync="newPasswordDialog">
          <el-row>
      <el-col :span='22'>
  <el-form>
    <el-form-item label="新的密码" label-width="100px">
      <el-input v-model="newPasswordOne" placeholder="请输入新的密码"/>
    </el-form-item>
    <el-form-item label="确认密码" label-width="100px">
      <el-input v-model="newPasswordTwo" placeholder="请确认新的密码"/>
    </el-form-item>
  </el-form>
      </el-col>
          </el-row>
  <div slot="footer" class="dialog-footer">
    <el-button type="primary" @click='clickInputNewPassword'>确认修改</el-button>
  </div>
</el-dialog>

    <el-row>
      <el-col :span="8">
        <!-- <cm-form
          ref="cmForm"
          :formList="formList"
          :buttonOption="buttonOption"
          @clickSubmit="clickSubmit"
          class="test"
        ></cm-form> -->
        <el-form :model="clientInfo" :rules="rules" ref="clientInfo" label-width="100px" class="demo-ruleForm">
          <el-form-item label="客户姓名" >
            <el-input v-model="clientInfo.name"></el-input>
          </el-form-item>
          <el-form-item label="客户住址" prop="address">
            <el-input v-model="clientInfo.address"></el-input>
          </el-form-item>
          <el-form-item label="客户职业" prop="occupation">
            <el-input v-model="clientInfo.occupation"></el-input>
          </el-form-item>
          <el-form-item label="客户生日" prop="birthdate">
            <el-date-picker
              v-model="clientInfo.birthdate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期">
            </el-date-picker>
          </el-form-item>          
          <el-form-item label=" ">
            <el-button type="primary" @click='clickSave'>保存</el-button>
            <el-button @click="modifyPassword" >修改密码</el-button>
          </el-form-item>
          <!-- <el-form-item label="客户住址" prop="region">
            <el-select v-model="ruleForm.region" placeholder="请选择活动区域">
              <el-option label="区域一" value="shanghai"></el-option>
              <el-option label="区域二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="活动性质" prop="type">
            <el-checkbox-group v-model="ruleForm.type">
              <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
              <el-checkbox label="地推活动" name="type"></el-checkbox>
              <el-checkbox label="线下主题活动" name="type"></el-checkbox>
              <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="特殊资源" prop="resource">
            <el-radio-group v-model="ruleForm.resource">
              <el-radio label="线上品牌商赞助"></el-radio>
              <el-radio label="线下场地免费"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="活动形式" prop="desc">
            <el-input type="textarea" v-model="ruleForm.desc"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item> -->
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import localStore from 'storejs'

export default {
  name: 'TestForm',
  data() {
    return {
      oldPassword:'',
      newPasswordOne:'',
      newPasswordTwo:'',
      clientInfo:{},
      oldPasswordDialog:false,
      newPasswordDialog:false,
      formList: [
        {
          itemType: 'input',
          model: '',
          label: '测试输入框',
          placeholder: '请输入',
          prop: 'testInput',
          rules: [
            { required: true, message: '不能为空' }
          ]
        },
        {
          itemType: 'select',
          model: '',
          label: '测试选择框',
          placeholder: '请选择',
          prop: 'testSelect',
          options: [{
            value: '选项1',
            label: '黄金糕'
          }, {
            value: '选项2',
            label: '双皮奶'
          }, {
            value: '选项3',
            label: '蚵仔煎'
          }, {
            value: '选项4',
            label: '龙须面'
          }, {
            value: '选项5',
            label: '北京烤鸭'
          }],
          rules: [
            { required: true, message: '不能为空' }
          ],
        },
        {
          itemType: 'radioGroup',
          model: '',
          label: '测试选择项',
          size: 'mini',
          prop: 'testRadio',
          radios: [{
            value: '选项1',
            label: '黄金糕'
          }, {
            value: '选项2',
            label: '双皮奶'
          }, {
            value: '选项3',
            label: '蚵仔煎'
          }, {
            value: '选项4',
            label: '龙须面'
          }, {
            value: '选项5',
            label: '北京烤鸭'
          }],
          dataAuth: () => {
            if (this.formList[1].model === '选项2') {
              this.formList[2].disabled = true
            } else {
              this.formList[2].disabled = false
            }
          }
        },
        {
          itemType: 'checkboxGroup',
          model: [],
          label: '测试多选框',
          prop: 'testCheckbox',
          checkboxs: [
            {
              label: '1',
              text: '321'
            },
            {
              label: '4',
              text: 'true',
            }
          ],
          dataAuth: () => {
            if (this.formList[1].model === '选项2') {
              this.formList[3].show = false
            } else {
              this.formList[3].show = true
            }
          }
        },
        {
          itemType: 'datePicker',
          label: '测试日期选择器',
          prop: 'testDate',
          model: '',
          valueFormat: 'yyyy-MM-dd',
          placeholder: '输入'
        },
        {
          itemType: 'switch',
          label: '测试开关',
          prop: 'testSwitch',
          model: ''
        }
      ],
      buttonOption: {
        showSubmit: true,
        showReset: true,
        submitText: '提交'
      },
      ruleForm: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '请选择活动区域', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
        ],
        type: [
          { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
        ],
        resource: [
          { required: true, message: '请选择活动资源', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '请填写活动形式', trigger: 'blur' }
        ]
      }
    }
  },
  mounted(){
    
    // console.log(localStore.get('userInfo'))
    // if(!this.$store.state.user.userInfo.name){
    //   console.log('read from local store')
    //   // this.$store.commit('set_userInfo',localStore.get('userInfo'))
    // }
    let tempquery=localStore.get('userInfo')
    // console.log(tempquery)
    this.api.getClientById(tempquery).then((res)=>{
      // console.log(res)
      this.$store.commit('set_userInfo',res[0])
      this.clientInfo=res[0]
    })
    
    // console.log(this.clientInfo)
  },
  methods: {
    clickSave(){
      this.api.putClientById(this.clientInfo).then(res=>{
        this.$message({
          message: '修改成功',
          type: 'success'
        })
        this.api.getClientById(this.clientInfo).then((res)=>{
          // console.log(res)
          this.$store.commit('set_userInfo',res[0])
          this.clientInfo=res[0]
        })
      })
    },
    modifyPassword(){
      this.oldPasswordDialog=true
    },
    clickInputOldPassword(){
      if(this.oldPassword===this.clientInfo.password){
        this.newPasswordDialog=true
        this.oldPasswordDialog=false
      }else{
        this.$message.error('密码错误')

      }
      
    },
    clickInputNewPassword(){
      if(this.newPasswordOne===this.newPasswordTwo){
        // todo
        // console.log('xiugai')
        this.$message({
          message: '修改成功',
          type: 'success'
        })
      }else{
        this.$message.error('请确认密码一致!')

      }
    },
    clickSubmit(...data) {
      console.log(data[0])
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
